.slideshow {
  cursor: e-resize;
  width: 50%;
}

.slideshow-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  border-top: 2px solid black;
}

.slideshow img {
  display: block;
  width: 100%;
}

@media (max-width: 700px) {
  .slideshow {
    width: 100%;
  }
}


